:root {
  --cl-lightest: #ffffff;
  --cl-lighter: #f4f4f4;

  --cl-primary: #1285ff;
  --cl-primary-dark: #2478cc;
  --cl-secondary: #49a55d;
  --cl-accent: #ffc700;
  --cl-grey: #595e67;
  --cl-greyer: #606060;
  --cl-greyest: #393939;
  --cl-dark: #0f131a;
  --cl-darker: #030303;
  --cl-darkest: #000000;

  /* FONT */
  --font-body: "Roboto", sans-serif;
  --font-heading: "Poppins", sans-serif;

  /* UTIL */
  --box-shadow: 0 2px 3px 1px #777;
}
