.days {
  box-shadow: 0px 0px 1px rgba(12, 26, 75, 0.24),
    0px 3px 8px -1px rgba(50, 50, 71, 0.05);
  padding: 32px;
  margin-bottom: 32px;

  &__title {
    color: #014eff;
    font-family: "Inter", Helvetica, sans-serif;
    font-size: 18px;

    display: flex;
    justify-content: space-between;

    margin-bottom: 16px;
  }

  &__progress {
    margin-bottom: 32px;
  }

  &__bar {
    margin-bottom: 16px;
    width: 100%;
  }

  &__label {
    color: var(--cl-grey);
    font-family: Arial, Helvetica, sans-serif;
    font-size: 11px;
    letter-spacing: 0.45px;
    text-align: center;
    display: block;
  }

  &__item {
    margin-bottom: 24px;
    font-size: 14px;

    &::before {
      content: url(data:image/svg+xml,%3Csvg%20width%3D%227%22%20height%3D%2211%22%20viewBox%3D%220%200%207%2011%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%3Cpath%20d%3D%22M1%209.5L5%205.5L1%201.5%22%20stroke%3D%22%23464E5B%22%20stroke-width%3D%222%22%2F%3E%0A%3C%2Fsvg%3E%0A);
      margin-right: 16px;
    }

    &--active {
      color: var(--cl-primary-dark);
      font-weight: 700;

      &::before {
        content: url(data:image/svg+xml,%3Csvg%20width%3D%228%22%20height%3D%229%22%20viewBox%3D%220%200%208%209%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%3Ccircle%20cx%3D%224%22%20cy%3D%224.5%22%20r%3D%224%22%20fill%3D%22%232478CC%22%2F%3E%0A%3C%2Fsvg%3E%0A);
      }
    }
  }
}
progress,          /* All HTML5 progress enabled browsers */
progress[role]     /* polyfill */ {
  /* Turns off styling - not usually needed, but good to know. */
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;

  /* gets rid of default border in Firefox and Opera. */
  border: none;

  /* Needs to be in here for Safari polyfill so background images work as expected. */
  background-size: auto;

  /* Dimensions */
  width: 400px;
  height: 8px;
}

/* Polyfill */
progress[role]:after {
  background-image: none; /* removes default background from polyfill */
}

/* Ensure fallback text doesn't appear in polyfill */
progress[role] strong {
  display: none;
}

progress[value]::-webkit-progress-bar {
  background-color: #eff6fa;
}
progress[value]::-webkit-progress-value {
  background-color: var(--cl-secondary);
  border-radius: 10px;
}

