.join {
  box-shadow: 0px 0px 1px rgba(12, 26, 75, 0.24),
    0px 3px 8px -1px rgba(50, 50, 71, 0.05);
  padding: 32px;

  &__container {
    width: fit-content;
    margin: 0 auto;
  }

  &__logo {
    font-size: 39.85px;
    letter-spacing: 5.81px;
    margin-bottom: 32px;

    &__text {
      padding-bottom: -4px;
      border-bottom: 2px solid var(--cl-accent);
    }

    &__plus {
