    }
  }

  &__lead {
    color: #4b4b4b;
    font-size: 18px;
    font-weight: 400;
    margin-bottom: 24px;
  }

  &__item {
    margin-bottom: 16px;
    display: flex;
    align-items: center;
    font-size: 12px;
    color: #5b5b5b;

    &::before {
      content: url(data:image/svg+xml,%3Csvg%20width%3D%2216%22%20height%3D%2216%22%20viewBox%3D%220%200%2016%2016%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%3Cpath%20d%3D%22M8%200C10.1217%200%2012.1566%200.842855%2013.6569%202.34315C15.1571%203.84344%2016%205.87827%2016%208C16%2010.1217%2015.1571%2012.1566%2013.6569%2013.6569C12.1566%2015.1571%2010.1217%2016%208%2016V16C5.87827%2016%203.84344%2015.1571%202.34315%2013.6569C0.842855%2012.1566%200%2010.1217%200%208V8C0%205.87827%200.842855%203.84344%202.34315%202.34315C3.84344%200.842855%205.87827%200%208%200V0Z%22%20fill%3D%22%2366CB9F%22%2F%3E%0A%3Cpath%20d%3D%22M4.55005%207.58003L6.94005%209.74003C7.027%209.82086%207.14133%209.86579%207.26005%209.86579C7.37877%209.86579%207.49309%209.82086%207.58005%209.74003L11.5%205.53003%22%20stroke%3D%22white%22%20stroke-miterlimit%3D%2210%22%20stroke-linecap%3D%22round%22%2F%3E%0A%3C%2Fsvg%3E%0A);
