.header {
  height: 161px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }
  &__logo {
    font-family: var(--font-heading);
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 3.07px;

    &::after {
      content: "";
      display: block;
      height: 2px;
      width: 45%;
      background-color: #f4af32;
      margin-top: 4px;
    }

    span {
      margin-left: 4px;
      color: #f4af32;
      font-size: 20px;
    }
  }
}

.banner {
  height: 40px;
  width: 100vw;
  background-color: var(--cl-primary-dark);
  color: var(--cl-lightest);
  font-size: 14px;

  display: flex;
  justify-content: center;
  align-items: center;

  padding: 8px;
}

.topnav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 0;

  &__search {
    width: 360px;
    height: 32px;
    border: none;
    border-radius: 4px;
    background-color: var(--cl-lighter);
  }
}

.btn-group {
  display: flex;
  justify-content: space-between;

  & > * {
    margin: 0 4px;
  }
}

.user {
  display: flex;
  align-items: center;

  &__name {
    display: flex;
    align-items: flex-end;
    font-size: 11.48px;
    &::after {
      content: "▼";
      display: block;
      font-size: 10px;
      padding: 0 4px;
    }
  }

  &__dp {
    height: 32px;
    width: 32px;
  }
}

.nav {
  padding-bottom: 32px;

  &__list {
    display: flex;
    width: 100%;
    justify-content: space-between;
  }

  &__item {
    font-size: 17px;
  }
}

.buger-menu {
  position: relative;
  display: none;
  margin-left: 8px;

  #menuToggle {
    display: block;
    z-index: 1;

    -webkit-user-select: none;
    user-select: none;
  }

  #menuToggle a {
    text-decoration: none;
    color: #232323;

    transition: color 0.3s ease;
  }

  #menuToggle input {
    display: block;
    width: 40px;
    height: 32px;
    position: absolute;

    cursor: pointer;

    opacity: 0; /* hide this */
    z-index: 2; /* and place it over the hamburger */

    -webkit-touch-callout: none;
  }

  /*
 * Make this absolute positioned
 * at the top left of the screen
 */
  #menu {
    position: absolute;
    width: 300px;
    margin-top: 32px;
    padding: 50px;
    top: 0;
    right: -40px;
    // height: 100vh;
    // width: 100vw;
    // padding-top: 125px;

    background: #ededed;
    list-style-type: none;
    -webkit-font-smoothing: antialiased;
    /* to stop flickering of text in safari */

    transform-origin: 0% 0%;
    transform: translate(120%, 0);

    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  }

  #menu li {
    padding: 10px 0;
    font-size: 22px;
  }

  /*
 * And let's slide it in from the left
 */
  #menuToggle input:checked ~ ul {
    transform: none;
  }
}

@media (max-width: 768px) {
  .banner {
    width: 100%;
    font-size: 12px;
    text-align: center;
  }
  .header {
    height: fit-content;

    &__logo {
      font-size: 14px;
    }
  }
  .topnav__search {
    display: none;
  }

  .btn-group {
    display: none;
  }
  .nav {
    display: none;
  }

  .buger-menu {
    display: block;
  }
}

@media (max-width: 576px) {
  .banner {
    width: 100%;
  }
  .wrapper {
    padding: 0 20px;
  }
  .content__row {
    flex-direction: column-reverse;
  }
}
