@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&family=Inter:wght@700;900&family=Roboto:wght@400;700&display=swap");

body {
  font-family: var(--font-body);
  color: var(--cl-darkest);
}

h1,
h2,
h3,
h4 {
  margin: 0;
  font-family: var(--font-heading);
  font-weight: 700;
  line-height: 1.3;
  text-transform: uppercase;
}

h1 {
  font-size: 1.802rem;
}

h2 {
  font-size: 1.424rem;
}

h3 {
  font-size: 1.266rem;
}

h4 {
  font-size: 1.125rem;
}

small,
.text_small {
  font-size: 0.889rem;
}

small,
.text_small {
  font-size: 0.75rem; //13.50px
}

p {
  margin-top: 0;
  font-weight: 400;
  line-height: 1.75;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  font-family: var(--font-heading);
  font-weight: 800;
  text-transform: uppercase;
}
