.footer {
  margin-bottom: 32px;

  &-nav__list {
    margin-bottom: 64px;
  }

  &-nav__item {
    color: var(--cl-grey);
    font-size: 14px;
    margin-bottom: 8px;
    width: fit-content;

    &--heading {
      color: var(--cl-primary);
      font-weight: 700;
    }

    &--accent {
      background-color: var(--cl-accent);
      color: var(--cl-darkest);
      letter-spacing: 0.35px;
      padding: 1px 2px;
      border-radius: 4px;
    }
  }

  &-end {
    display: flex;
    justify-content: space-between;
  }

  &-social {
    display: flex;

    &__nav {
      display: flex;
    }

    &__item {
      display: inline-block;
      margin-left: 24px;
    }
  }

  &__logo {
    font-size: 21.06px;
    letter-spacing: 3.07px;

    &::after {
      content: "";
      display: block;
      height: 4px;
      width: 115px;
      margin-top: 4px;
      background-color: var(--cl-accent);
    }
  }

  hr {
    margin-bottom: 16px;
  }
}

@media (max-width: 768px) {
  .footer {
    &__logo {
      font-size: 14px;
    }
  }
}

@media (max-width: 576px) {
  .footer {
    &__logo {
      font-size: 14px;
    }

    &-social {
      flex-direction: column;

      &__nav {
        flex-wrap: wrap;
      }

      &__item {
        margin-bottom: 16px;
      }
    }
  }
}
