.wrapper {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 68px;
}

@media (max-width: 768px) {
  .wrapper {
    padding: 0 40px;
  }
  .content__row {
    flex-direction: column-reverse;
  }
}

@media (max-width: 576px) {
  .wrapper {
    padding: 0 20px;
  }
  .content__row {
    flex-direction: column-reverse;
  }
}
