.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 24px;
  width: fit-content;
  padding: 10px 8px;
  background-color: var(--cl-primary);
  border-radius: 4px;
  font-weight: 700;
  cursor: pointer;
}

.btn--small {
  font-size: 10px;
  text-transform: uppercase;
  border-radius: 3px;
}

.btn--tiny {
  font-size: 9px;
  font-weight: 400;
  text-transform: uppercase;
  border-radius: 12px;
  height: 21px;
  background-color: var(--cl-lightest);
  color: #464e5b;
  margin-right: 16px;
}

.btn--outline {
  background-color: unset;
  border: 1px solid var(--cl-primary-dark);
  color: var(--cl-primary-dark);
}

.btn--primary {
  background-color: var(--cl-primary);
  color: var(--cl-lightest);
}

.btn--accent {
  background-color: var(--cl-accent);
}
