* {
  box-sizing: border-box;
}

body {
  background-color: var(--cl-light);
  overflow-x: hidden;
  width: 100%;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

img {
  width: 100%;
}

button {
  border: none;
  background-color: unset;
}

.util--text-center {
  text-align: center;
}
