.main {
  margin-bottom: 32px;
}

.video {
  margin-bottom: 32px;

  &__title {
    font-size: 24px;
    font-weight: 500;
    letter-spacing: 0.25px;
    text-transform: none;
    margin-bottom: 32px;
  }

  &__player {
    width: 100%;
    height: 393.21px;
    border-radius: 5.59px;
  }
}

.details {
  display: flex;
  flex-direction: column;
  margin-bottom: 32px;

  &__download {
    height: 34px;
    width: 165px;
    border: 1px solid var(--cl-primary);
    color: var(--cl-primary);
    font-family: var(--font-body);
    font-size: 12px;
    text-transform: capitalize;
    align-self: flex-end;
    margin-bottom: 8px;
  }

  &__format {
    color: var(--cl-greyer);
    font-size: 7.94px;
    align-self: flex-end;
    letter-spacing: 0.16px;
    display: block;
  }

  &__views {
    color: var(--cl-greyer);
    font-size: 13px;
    letter-spacing: 0.2px;
    display: block;
  }

  &__row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
  }

  &__name {
    color: var(--cl-darker);
    font-family: var(--font-body);
    font-size: 14px;
    letter-spacing: 0.25px;
    text-transform: none;
    font-weight: 400;
  }

  &__label {
    color: var(--cl-greyer);
    font-size: 12px;
    letter-spacing: 0.3px;
  }

  &__call {
    color: var(--cl-darker);
    font-size: 11px;
    letter-spacing: 0.25px;
    text-transform: uppercase;
    span:not(:last-of-type) {
      margin-right: 4px;
    }
  }

  &__desc {
    color: var(--cl-greyest);
    font-size: 14px;
    letter-spacing: 0.2px;
  }

  &__more {
    color: var(--cl-primary);
    font-size: 12px;
    font-weight: 700;
  }

  hr {
    margin-bottom: 16px;
  }
}

.next {
  display: flex;
  justify-content: space-between;
  margin-bottom: 32px;

  &__btn {
    font-family: var(--font-body);
    font-size: 14px;
    letter-spacing: 0.14px;
    height: 39px;
    width: 273px;
  }

  &__lesson {
    font-size: 16px;

    &::after {
      content: url(data:image/svg+xml,%3Csvg%20width%3D%2216%22%20height%3D%2216%22%20viewBox%3D%220%200%2016%2016%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%3Cpath%20d%3D%22M5.65058e-06%209.00048L12.17%209.00049L6.58%2014.5905L8%2016.0005L16%208.00049L8.00001%200.000486183L6.59001%201.41049L12.17%207.00049L6.17511e-06%207.00048L5.65058e-06%209.00048Z%22%20fill%3D%22%232478CC%22%2F%3E%0A%3C%2Fsvg%3E%0A);
      padding-left: 4px;
    }
  }
}

.total-comments {
  display: flex;
  align-items: center;
  margin-bottom: 32px;
  &__count {
    color: var(--cl-darker);
    font-family: var(--font-body);
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 0.1px;
    margin: 0;
    margin-right: 32px;
  }

  svg {
    margin-right: 8px;
  }

  span {
    color: var(--cl-greyer);
    font-size: 14px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
  }
}

.comment {
  display: flex;
  margin-bottom: 32px;

  &__dp {
    height: 40px;
    width: 40px;
    margin-right: 16px;
  }

  &__input {
    border: none;
    border-bottom: 1px solid #e9e9e9;
    width: 100%;

    &::placeholder {
      color: var(--cl-greyer);
      font-size: 14px;
      letter-spacing: 0.2px;
    }
  }

  &__details {
    font-size: 13px;
    align-self: baseline;
  }

  &__username {
    color: var(--cl-darker);
    font-size: 13px;
    font-weight: 400;
    margin: 0 0 4px;

    span {
      color: var(--cl-greyer);
      font-size: 12px;
      padding-left: 4px;
    }
  }

  &__message {
    color: var(--cl-darker);
  }

  &__rating {
    display: flex;
    align-items: center;

    & > * {
      margin-right: 16px;
    }
  }

  &__likes {
    display: flex;
    align-items: center;
    margin-bottom: 16px;

    &__icon {
      margin-right: 4px;
    }
  }

  &__reply {
    margin-left: 20px;
  }

  &__replies {
    display: flex;
    align-items: center;
  }

  &__view {
    font-size: 14px;
    color: #065fd4;
    letter-spacing: 0.5px;
    margin-left: 8px;
  }
}

.resources {
  &__title {
    color: var(--cl-dark);
    font-size: 24px;
    font-weight: 500;
    letter-spacing: 0.25px;
    text-transform: none;
  }

  &__lead {
    color: var(--cl-grey);
    font-size: 18px;
  }
}

.files {
  border-radius: 4px;
  box-shadow: 0px 0px 1px rgba(12, 26, 75, 0.24),
    0px 3px 8px -1px rgba(50, 50, 71, 0.05);
  margin-bottom: 64px;
  padding: 16px 28px;

  hr {
    margin-bottom: 8px;
  }
}

.file {
  display: flex;
  justify-content: space-between;
  padding: 8px 0;

  div {
    display: flex;
    align-items: center;
  }

  &__name {
    font-family: "Inter", Helvetica, sans-serif;
    font-weight: 900;
    letter-spacing: 0.19px;
    color: var(--cl-dark);
    display: block;
    width: fit-content;
    margin-left: 16px;

    &::after {
      content: "";
      display: block;
      height: 2px;
      width: 100%;
      background-color: var(--cl-dark);
    }
  }

  &__btn {
    color: var(--cl-lightest);
    font-size: 12px;
    font-weight: 700;
    letter-spacing: 0.14px;
    text-transform: none;
    height: 32px;
    width: 128px;
  }
}

.apply {
  text-align: center;
  border: 1px solid var(--cl-secondary);
  border-radius: 8px;
  padding: 32px 84px;

  &__title {
    font-weight: 400;
    font-size: 24px;
    letter-spacing: 0.92px;
    text-transform: none;
    stroke: 1px solid #f0f0f0;
    margin-bottom: 8px;
  }

  &__hero {
    font-size: 32px;
    letter-spacing: 0.92px;
    stroke: 1px solid #f0f0f0;
    text-transform: none;
    margin-bottom: 32px;
  }

  &__para {
    font-family: 16px;
    letter-spacing: 1px;
    stroke: 1px solid #f0f0f0;
    text-transform: lowercase;
  }

  &__btn {
    background-color: var(--cl-secondary);
    color: var(--cl-lightest);
    font-size: 14px;
    letter-spacing: 0.14px;
    text-transform: none;
    height: 44px;
    width: 240px;
    margin: 0 auto;
  }
}

hr {
  border-top: 0.7px solid #e6e6e6;
  border-bottom: none;
  margin: 8px 0 32px;
}

@media (max-width: 576px) {
  .main {
    margin-top: 32px;
  }
  .apply {
    padding: 32px 16px;
    &__btn {
      width: fit-content;
    }
  }

  .details {
    &__format {
      margin-bottom: 16px;
    }
    &__author {
      width: 100%;
    }
  }

  .next {
    &__lesson {
      text-align: right;
    }
  }
}
